import React from "react";
import { Box, Heading, Text } from "rebass";
import { fontSize, space } from "../../styles/theme";

import { Uploader } from "../Uploader/Uploader";
import { RouteRecord } from "./RouteRecord";
import { useSelector } from "react-redux";
import { getRoutes } from "../../state/routes/routesReducer";

export const RouteStep: React.FC = () => {
  const routes = useSelector(getRoutes);
  const amountOfRoutes = Object.keys(routes).length;

  return (
    <>
      <Text
        fontSize={fontSize["16"]}
        // fontWeight='bold'
        color="text"
        sx={{
          textAlign: "justify",
          whiteSpace: "pre-line",
          marginBottom: space["32"],
        }}
      >
        <p>
          Witaj w aplikacji przeznaczonej do analizy tras <code>.gpx</code>.
          Dziki interaktywnej mapie porwnasz zaadowane trasy z wyznaczonym
          torem, otrzymujc szczegowe informacje i podsumowanie dla kadej z
          nich.
        </p>

        <p>
          Projekt powsta na potrzeby organizacji amatorskich zawodw lotniczych
          przez czonkw Aeroklubu Szczeciskiego.
        </p>
      </Text>

      <Box variant="container">
        <Uploader />
      </Box>

      {amountOfRoutes > 0 && (
        <Box variant="container">
          <Heading variant="heading.h3">Zaadowane przebiegi</Heading>
        </Box>
      )}

      {amountOfRoutes === 0 && (
        <Text>
          Nie zaadowano adnych tras, przecignij pliki <code>.gpx</code> do
          okna powyej, lub uyj przegldarki plikw.
        </Text>
      )}

      {Object.entries(routes).map(([_key, route]) => (
        <RouteRecord key={`route-${route.id}`} route={route} />
      ))}
    </>
  );
};
